.scoreBoard {

    height: 50px;
    border-bottom: 4px solid white;
    border-left: 4px solid white;
    border-right: 4px solid white;
    width: 98%;
    color:white;
    line-height: 50px;
    padding-left:1%;
    padding-right:1%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.scoreBoard span {
    font-size: 26px;
    display:-webkit-flex;
    display:flex;
}

.summaryTable {
    position: absolute;
    top: -50px;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    height: 40%; /*requires explicit height*/
    width: 50%;
    z-index: 200;
    background: Black;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 5px;
}

.modalContainer {
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 180;
    position: absolute;

}

